@import url(../../fonts/fonts.css);
.paddings {
  padding-left: 20px;
  padding-right: 20px;
}
.url {
  display: block;
  color: white;
  text-decoration: none;
  text-transform: uppercase;
  font-family: 'league_gothic_regular', sans-serif;
}
body {
  width: 100%;
  height: 100%;
  max-height: 800px;
  font-family: 'open_sans_light', Tahoma, sans-serif;
  font-size: 16px;
  line-height: 24px;
  background-image: url(../../img/body-bg.jpg);
  background-position-x: center;
  background-repeat: no-repeat;
}
main.container-fluid {
  padding-left: 0;
  padding-right: 0;
}
.row {
  margin-right: 0px;
  margin-left: 0px;
}
h1,
h2,
h3 {
  font-family: 'league_gothic_regular', sans-serif;
}
header {
  width: 100%;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 60px;
}
header .logo {
  display: block;
  font-size: 0px;
  width: 155px;
  height: 39px;
  margin-bottom: 25px;
  margin-left: auto;
  margin-right: auto;
}
header .logo img {
  width: 155px;
  height: 39px;
}
.main-nav ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: row;
}
.main-nav ul li a {
  display: block;
  color: white;
  text-decoration: none;
  text-transform: uppercase;
  font-family: 'league_gothic_regular', sans-serif;
  font-size: 24px;
}
.main-nav ul li a:hover,
.main-nav ul li a .active {
  border-bottom: 2px solid #edbfff;
}
.slider .carousel-indicators {
  bottom: -50px;
}
.slider .carousel-indicators li {
  background-color: rgba(0, 0, 0, 0.3);
  border: none;
  width: 15px;
  height: 15px;
}
.slider .carousel-indicators .active {
  background-color: white;
  width: 15px;
  height: 15px;
  margin: 1px;
  border: 3px solid rgba(0, 0, 0, 0.6);
}
.slider .carousel-control.left,
.slider .carousel-control.right {
  background-image: none;
}
.slider .wrapper-slider {
  margin-top: 30px;
  margin-bottom: 100px;
  z-index: 1;
}
.slider .wrapper-slider .item {
  height: 200px;
}
.slider h1,
.slider h2 {
  text-align: center;
  line-height: 24px;
}
.slider h1 {
  font-size: 36px;
  color: white;
}
.slider h2 {
  font-size: 18px;
  color: #edbfff;
}
.about {
  width: 100%;
  padding-left: 12px;
  padding-right: 12px;
  padding-top: 75px;
  padding-bottom: 60px;
  background-image: linear-gradient(to top, #322539 0%, #745083 100%);
  color: white;
}
.about article {
  padding-left: 51px;
  margin-bottom: 50px;
  position: relative;
}
.about article:before {
  content: '';
  position: absolute;
  width: 35px;
  height: 33px;
  top: 0;
  left: 0;
  background-image: url(../../img/about-sprite.png);
  background-repeat: no-repeat;
}
.about article a {
  display: block;
  color: white;
  text-decoration: none;
  text-transform: uppercase;
  font-family: 'league_gothic_regular', sans-serif;
  float: right;
}
.about article a:hover {
  text-shadow: 0 0 8px rgba(255, 255, 255, 0.8);
}
.content {
  width: 100%;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 90px;
  padding-bottom: 30px;
  background-color: #f9f9f9;
  box-shadow: inset 0px 2px 4px 0px rgba(0, 0, 0, 0.5);
}
.content .review {
  background-color: white;
  padding: 35px;
  position: relative;
  margin-bottom: 60px;
}
.content .review:before {
  content: '';
  display: block;
  position: absolute;
  width: 49px;
  height: 49px;
  background-image: url(../../img/review-img.png);
  top: 34px;
  left: -19px;
}
.content .review .title {
  font-family: 'league_gothic_regular', sans-serif;
  font-size: 24px;
  text-transform: uppercase;
}
.content .review p:first-child {
  color: black;
  margin-bottom: 0px;
}
.content .review p:nth-child(2) {
  color: #edbfff;
}
.content .articles {
  padding-left: 0px;
  padding-right: 0px;
}
.content .articles article {
  padding-left: 55px;
  background-image: url(../../img/articles-img.png);
  background-repeat: no-repeat;
}
.content .articles h4 {
  font-family: 'open_sans_extrabold', sans-serif;
  font-size: 16px;
}
.content .articles p {
  font-size: 16px;
}
.content .articles a {
  display: block;
  color: white;
  text-decoration: none;
  text-transform: uppercase;
  font-family: 'league_gothic_regular', sans-serif;
  color: black;
  float: right;
}
.content .articles a:hover {
  border-bottom: 2px solid #edbfff;
}
.content .logos {
  margin-top: 90px;
}
.content .logos ul {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: center;
  text-align: center;
  flex-wrap: wrap;
}
.content .logos ul li {
  width: 70px;
  height: 64px;
  margin-right: 25px;
  margin-left: 25px;
  margin-bottom: 25px;
  background-image: url(../../img/logos-sprite.png);
}
.content .logos ul li:nth-child(2) {
  background-position-x: -910px;
}
.content .logos ul li:nth-child(3) {
  background-position-x: -1042px;
}
.content .logos ul li:nth-child(4) {
  background-position-x: -1179px;
}
.content .logos ul li:nth-child(5) {
  background-position-x: -1300px;
  width: 110px;
  margin-right: 10px;
}
.content .logos ul li:last-child {
  background-position-x: -1472px;
  margin-left: 10px;
}
.categories {
  width: 100%;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 90px;
  padding-bottom: 90px;
  background-color: #ececec;
  border-top: 1px solid #d3d3d3;
}
.categories .hidden-below-960 {
  display: none;
}
footer {
  width: 100%;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 40px;
  background-image: url(../../img/footer-bg.jpg);
}
footer p {
  color: white;
  text-align: center;
}
footer .wrapper-footer {
  margin-bottom: 70px;
}
.to-top {
  display: none;
  position: fixed;
  top: 600px;
  right: 20px;
  border: none;
  cursor: pointer;
  transform: rotate(-90deg);
  width: 45px;
  height: 45px;
  background: rgba(0, 0, 0, 0.3);
}
.to-top:hover {
  border: 1px solid #edbfff;
}
.to-top-show {
  display: block;
}
@media screen and (min-width: 480px) {
  .slider {
    position: relative;
  }
  .slider::before {
    content: "";
    display: block;
    position: absolute;
    top: 10%;
    bottom: 10%;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.3);
  }
  .slider h1,
  .slider h2 {
    text-align: left;
  }
  .slider .carousel-control {
    width: 10%;
  }
  .slider .carousel-caption {
    float: left;
    position: relative;
    padding-top: 130px;
    text-align: left;
    width: 203px;
    left: 8%;
  }
  .slider .img-container {
    background-image: url(../../img/iphone-img-480.png);
    background-repeat: no-repeat;
    width: 192px;
    height: 304px;
    float: right;
    margin-right: 45px;
  }
}
@media screen and (min-width: 480px) and (max-width: 767px) {
  .visible-480 {
    display: inline!important;
  }
  .visible-480-block {
    display: block!important;
  }
  .content .articles {
    padding-left: 15%;
  }
  .content .articles a {
    padding-right: 10%;
  }
  .content .review .title {
    display: inline-block;
  }
}
@media screen and (max-width: 767px) {
  .header-nav ul li:first-child {
    margin-left: auto;
    margin-right: auto;
  }
  .header-nav ul li:nth-child(n+2) {
    display: none;
  }
  .header-popup {
    position: absolute;
    width: 250px;
    left: 50%;
    margin-left: -125px;
    text-align: center;
    background-color: #4d354f;
    border-radius: 8px;
    opacity: 0.9;
    z-index: 2;
  }
  .header-popup ul {
    flex-direction: column;
  }
  .header-popup ul li {
    border-bottom: 2px solid #051116;
  }
  .header-popup ul li:nth-child(n+2) {
    display: block;
  }
  .header-popup ul li:first-child {
    margin-left: 0px;
    margin-right: 0px;
  }
  .header-popup ul li:first-child a {
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
  }
  .header-popup ul li:last-child {
    border-bottom: none;
  }
  .header-popup ul li:last-child a {
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
  }
  .header-popup ul li a {
    padding-top: 7px;
    padding-bottom: 7px;
  }
  .header-popup ul li a:hover {
    background-color: #251628;
    border-bottom: 2px solid #edbfff;
  }
}
@media screen and (min-width: 768px) {
  header .logo {
    float: left;
  }
  header .header-nav {
    clear: both;
  }
  header .header-nav a {
    font-size: 24px;
    margin-right: 20px;
  }
  .slider .carousel-control {
    width: 50px;
  }
  .slider .carousel-indicators {
    margin-left: 0;
    width: 60px;
    left: 70%;
  }
  .slider .carousel-caption {
    width: 272px;
    left: 8%;
  }
  .slider .carousel-caption .text {
    font-size: 16px;
    font-family: 'open_sans_light', Tahoma, sans-serif;
  }
  .slider .carousel-control .glyphicon-chevron-right {
    right: 30%;
  }
  .slider .carousel-control .glyphicon-chevron-left {
    left: 30%;
  }
  .slider .img-container {
    background-image: url(../../img/iphone-img.png);
    width: 274px;
    height: 434px;
    margin-right: 60px;
  }
  .about article h3 {
    margin-top: 0px;
  }
  .content .logos ul {
    justify-content: space-between;
  }
  .content .logos ul li {
    margin-left: 0px;
    margin-right: 0px;
  }
  .footer-nav a {
    font-size: 20px;
    margin-right: 20px;
  }
  footer p {
    float: right;
    padding-right: 20px;
  }
}
@media screen and (min-width: 960px) {
  header .logo {
    display: inline-block;
  }
  header .header-nav {
    float: right;
    clear: right;
  }
  .about article:nth-child(2)::before {
    background-position-x: -340px;
  }
  .about article:last-child::before {
    background-position-x: -675px;
  }
  .content .review .title {
    display: inline-block;
  }
  .categories .hidden-upper-960 {
    display: none;
  }
  .categories .visible-960 {
    display: inline!important;
  }
  .categories article h3 {
    font-size: 30px;
  }
  .categories .hidden-below-960 {
    display: block;
    padding-top: 23px;
  }
  .categories .hidden-below-960 img {
    width: 133px;
    height: 37px;
  }
  .categories .hidden-below-960 p:first-of-type {
    margin-top: 15px;
    font-style: italic;
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-size: 16px;
    line-height: 24px;
    color: #c5c5c5;
    text-shadow: 1px 1px 0 #ffffff;
  }
  .footer-nav {
    float: left;
  }
}
@media screen and (min-width: 980px) {
  .centrator {
    width: 960px;
    margin-left: auto;
    margin-right: auto;
  }
  .wrapper-header,
  .wrapper-slider,
  .wrapper-about,
  .wrapper-content,
  .wrapper-categories,
  .wrapper-footer,
  .logos {
    width: 960px;
    margin-left: auto;
    margin-right: auto;
  }
}
